@font-face {
	font-family: 'jh-icons';
	src: url('fonts/jh-icons.ttf?364j9q') format('truetype'),
	url('fonts/jh-icons.woff?364j9q') format('woff'),
	url('fonts/jh-icons.svg?364j9q#jh-icons') format('svg');
	font-weight: normal;
	font-style: normal;
}

[class^="jh-icon"], [class*=" jh-icon"] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'jh-icons' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.jh-iconlink:before {
	content: "\e9cb";
}
